table {
    border: 2px solid #ffffff;
    width: 98%;
    border-collapse: collapse;
}
th {
    border: 2px solid #ffffff;
    padding: 2px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #fff;
}
td {
    border: 2px solid #ffffff;
    padding: 2px;
    color: #fff;
}
.button:hover {
    cursor: pointer;
}