body {
  height: 100%;
}

.main {
  background-color: #493D9E;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  position: relative;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
}

.headerList {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 15px;
}

.headerItemList {
  padding: 8px 18px;
  background-color: #B2A5FF;
  border-radius: 10px;
}

.headerItemList span {
  color: #fff;
}

.headerItemList:hover {
  background-color: #DAD2FF;
  cursor: pointer;
}

.contentContainer {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  align-items: center;
  margin-top: 20px;
}

button:hover{
  cursor: pointer;
}